/* src/LandingPage.css */
.landing-page {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
    padding: 20px;
  }
  
  .left-side {
    width: 50%;
    padding: 20px;
    box-sizing: border-box;
  }
  
  .right-side {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .right-side img {
    max-width: 100%;
    height: auto;
  }
  